// ** React Imports
import { Fragment, useState, useEffect } from 'react'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'

import Breadcrumbs from '@components/breadcrumbs'


// ** Store & Actions
import { getAllWallData, deleteFeedPost, editPostData, Feedtogglestatus } from '../store/action'
import { useSelector, useDispatch } from 'react-redux'


// ** Third Party Components
import ReactPaginate from 'react-paginate'
import { ChevronDown, MessageCircle, Heart, Edit, Trash2, XCircle, MoreVertical} from 'react-feather'
import DataTable from 'react-data-table-component'
import UiLoader from '@components/ui-loader'
import { Card, Row, Col, CustomInput, Modal, ModalHeader, ModalBody, ModalFooter, Button,  DropdownToggle, DropdownMenu, DropdownItem, UncontrolledDropdown } from 'reactstrap'

// ** Styles
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import { useHistory } from 'react-router-dom'

// ** Table Header

  const CustomHeader = () => {
  return (
    <div className="invoice-list-table-header w-100 mr-1 ml-50 mt-2 mb-75">
      <Row>
        
        <Col
          xl="6"
          className="d-flex align-items-sm-center justify-content-lg-end justify-content-start flex-lg-nowrap flex-wrap flex-sm-row flex-column pr-lg-1 p-0 mt-lg-0 mt-1"
        >
          
        </Col>
      </Row>
    </div>
  )
}

const UsersList = () => {
  // ** Store Vars
  const dispatch = useDispatch()
  const store = useSelector((state) => state.feeds)
  const history = useHistory()
  // ** States
  const [searchTerm, setSearchTerm] = useState('')
  const [listData, setListData] = useState([])
  const [currentPage, setCurrentPage] = useState(1)
  const [scrollModal, setScrollModal] = useState(false)
  const users = ["user-1", "user-2", "user-3", "user-4", "user-5"]
  const [currentRole] = useState({
    value: '',
    label: 'Select Role'
  })
  const [currentPlan] = useState({
    value: '',
    label: 'Select Plan'
  })
  const [currentStatus] = useState({
    value: '',
    label: 'Select Status',
    number: 0
  })

  // ** Function to toggle sidebar
  const toggleSidebar = () => history.push('/walls_add')
  const MySwal = withReactContent(Swal)
  useEffect(() => {
    dispatch(getAllWallData(currentPage))
    
  }, [currentPage])

  useEffect(() => {
    if (store.feedAllData) {
      setListData(store.feedAllData)
    }
  }, [JSON.stringify(store.feedAllData)])

console.log(store)
// useEffect(() => {
// // dispatch(Feedtogglestatus)
// }, [])

  const handleConfirmText = (id) => {
    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ml-1'
      },
      buttonsStyling: false
    }).then(function (result) {
      
     if (result.value) {
        dispatch(deleteFeedPost(id))       
      }
    })
  }

  
  const editHandler = (row) => {
    dispatch(editPostData(row))
    history.push(`/walls/edit/${row.feed_id}`)
  }
  const SwitchClick = (row) => {
    
    dispatch(Feedtogglestatus(row))
   
  }

  const formatDate = (value, formatting = { month:'short', day:'numeric', year:'numeric', hour:'numeric', minute:'numeric', second:'numeric' }) => {
    if (!value) return value
    return new Intl.DateTimeFormat('en-GB', formatting).format(new Date(value))
  }
console.log(store)
  const columns = [
    {
        name: '#',
        minWidth: '10px',
        selector: '#',
        sortable: true,
        cell: (row, pos) => `#${store.wall_page.from + pos}`
      }, 
      {
        name: 'Image',
        minWidth: '100px',
        selector: 'feed_image',
        sortable: true,
        cell: (row) => (
          <img src={row.feed_image} style={{ height: '50px', width: '100px'}} />
        )
      },
      {
        name: 'Type',
        minWidth: '160px',
        selector: 'type',
        sortable: true,
        cell: (row) => row.feed_type
      },
      {
        name: 'Engagement',
        minWidth: '160px',
        selector: 'engagement',
        sortable: true,
        cell: (row) => (
                      <div
                      // color='flat-primary'
                      style={{cursor : 'pointer'}}
            className='user-name text-truncate mb-0'
            onClick={() => setScrollModal(!scrollModal)}
          >
          <div className="d-flex">
            <span className='d-block font-weight-bold text-truncate'>{(row.full_name === " " ? "-" : row.full_name)}</span>
            <div className="pr-2"><MessageCircle width="14" /> {row.feed_comments}</div>
            <div><Heart width="14" /> {row.feed_likes}</div>        
          </div>
            </div>
        )
      },
      {
        name: 'Date & Time',
        minWidth: '160px',
        selector:'countdown',
        sortable: true,
        cell: (row) => formatDate(row.updated_on)
      
      },
      {
        name: 'Status',
        minWidth: '138px',
        selector: 'active',
        sortable: true,
        cell: (row) => (
          <div className="column-action">
            {
             
              (
                <CustomInput
              className='custom-control-success'
              type='switch'
              id={`success${row.feed_id}`}
              name='success'
              onChange={() => SwitchClick(row.feed_id)}
              inline
              defaultChecked = {row.status !== 'DL'}
            />
              )
            }
           
          
          </div>
        )
      },
      {
        name: 'Active',
        minWidth: '138px',
        selector: 'active',
        sortable: true,
        cell: (row) => (
          <UncontrolledDropdown>
          <DropdownToggle className='pr-1' tag='span'>
            <MoreVertical size={15} />
          </DropdownToggle>
          <DropdownMenu right>
            <DropdownItem tag='a'  className='w-100' onClick={() => editHandler(row)} >
              <Edit size={15}/>
              <span className='align-middle ml-50'>Edit</span>
            </DropdownItem>
            {/* <DropdownItem tag='a' href='/' className='w-100' onClick={e => e.preventDefault()}>
              <Archive size={15} />
              <span className='align-middle ml-50'>Archive</span>
            </DropdownItem> */}
            <DropdownItem tag='a' className='w-100' onClick={() => handleConfirmText(row.feed_id)}>
              <Trash2 size={15} />
              <span className='align-middle ml-50'>Delete</span>
            </DropdownItem>
          </DropdownMenu>
        </UncontrolledDropdown>
        )
      }
     
    ]


  // ** User filter options

  // ** Function in get data on page change
  const handlePagination = (page) => {
    setCurrentPage(page.selected + 1)
  }


  const handleFilter = (val) => {
    setSearchTerm(val)
  }

  if (store.loadFeedAll) {
    return (
      <div className="position-absolute d-flex justify-content-center align-items-center w-100 position-top-0 position-bottom-0 position-left-0 position-right-0 h-100">
        <UiLoader blocking />
      </div>
    )
  }
console.log(store.feedAllData)
console.log(store.wall_page)
  
  const CustomPagination = () => {
    
    const count = store.wall_page.last_page
    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        pageCount={count || 1}
        activeClassName="active"
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={(page) => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        containerClassName={
          'pagination react-paginate justify-content-end my-2 pr-1'
        }
      />
    )
  }
  
  
  // ** Table data to render
  const dataToRender = () => {
    const filters = {
      role: currentRole.value,
      currentPlan: currentPlan.value,
      status: currentStatus.value,
      q: searchTerm
    }

    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })

    if (listData.length === 0 && isFiltered) {
      return []
    } else {
      return listData
    }
  }

  return (
    <Fragment>
      <Breadcrumbs breadCrumbTitle='Walls' breadCrumbActive='Walls' />
      <Card>
        <DataTable
          noHeader
          pagination
          subHeader
          responsive
          paginationServer
          columns={columns}
          sortIcon={<ChevronDown />}
          className="react-dataTable"
          paginationPerPage
          paginationDefaultPage={currentPage}
          paginationComponent={CustomPagination}
          data={dataToRender()}
          subHeaderComponent={
            <CustomHeader
              toggleSidebar={toggleSidebar}
              // handlePerPage={handlePerPage}
              rowsPerPage
              searchTerm={searchTerm}
              handleFilter={handleFilter}
            />
          }
        />
      </Card>
      <Modal isOpen={scrollModal} toggle={() => setScrollModal(!scrollModal)} className='modal-dialog-centered'>
          <ModalHeader toggle={() => setScrollModal(!scrollModal)}>Users Comments</ModalHeader>
          <ModalBody>             
              {users.map(user => (
                <tr key={user}>
                  <td>
                    <div className='mt-1'>
                      <Col>
                      <Row>
                      {user}
                      <Trash2 className='ml-2'/>
                      <XCircle className='ml-2' />
                      </Row>   
                      </Col>
                    </div>
                    </td>
                </tr>
             ))}
            {/* <p>
              Biscuit powder jelly beans. Lollipop candy canes croissant icing chocolate cake. Cake fruitcake powder
              pudding pastry.
            </p>
            <p>
              Chocolate cake icing tiramisu liquorice toffee donut sweet roll cake. Cupcake dessert icing dragÃ©e
              dessert. Liquorice jujubes cake tart pie donut. Cotton candy candy canes lollipop liquorice chocolate
              marzipan muffin pie liquorice.
            </p> */}
          </ModalBody>
          <ModalFooter>
            <Button color='primary' onClick={() => setScrollModal(!scrollModal)}>
              Done !
            </Button>
          </ModalFooter>
        </Modal>
    </Fragment>
  )
}

export default UsersList
